@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Poiret+One&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#ff6347}.App{text-align:center}.App #title{font-family:"Press Start 2P", cursive;color:#06720f;text-shadow:2px 2px 0 #fff;font-size:1.5em;margin-top:30px}.App #description{font-family:"Press Start 2P", cursive;color:#fff;font-size:0.9em;text-shadow:2px 2px 0 #06720f;margin-top:20px}.App svg{background-color:#ff6347;border-radius:5px;box-shadow:rgba(0,0,0,0.2) 0px 60px 40px -7px}.App #canvas{min-width:1000px;min-height:600px}.App #legend{margin-top:30px;min-width:265px;min-height:256px}.App #legend text{font-weight:500}.App #tooltip{visibility:hidden;position:absolute;background-color:rgba(255,253,208,0.8);padding:10px;text-align:center;font-size:0.8em;font-weight:500;border-radius:10px}.App #da3ker{color:white;font-size:1em;letter-spacing:2px;font-family:"Rampart One", cursive}

